const FlashNotice = {
    queue: [],
    idle: true,
    push(message) {
        this.queue.push(message);
        this.next();
    },
    next() {
        if (!this.idle) {
            return;
        }
        const message = this.queue.join("<br/><br/>");
        this.queue = [];
        if (!message) {
            return;
        }
        this.flash(message);
    },
    flash(message) {
        return __awaiter(this, void 0, void 0, function* () {
            this.idle = false;
            const self = this;
            const toast = yield toastController
                .create({
                message: message,
                duration: 2000,
                color: 'dark',
                translucent: true,
                position: 'bottom'
            });
            toast.present();
            toast.onDidDismiss().then(() => {
                self.idle = true;
                self.next();
            });
        });
    }
};
const flash = (message) => {
    FlashNotice.push(message);
};
let globalAlertPrompt;
const alert = (message, title) => __awaiter(void 0, void 0, void 0, function* () {
    Topic.publish('dismissModal');
    if (globalAlertPrompt) {
        globalAlertPrompt.dismiss();
    }
    globalAlertPrompt = yield alertController
        .create({
        header: title,
        message: message,
        translucent: true,
        buttons: ['Ok'],
    });
    return globalAlertPrompt.present();
});
